import Vue from 'vue'
import { dateFtt } from '@/utils/tools'

Vue.filter('solvePlaceholder', (type) => {
  return type == 'input'?'请输入':type=='select'?'请选择':''
})

Vue.filter('solveKey', (item) => {
  return JSON.stringify(item)
})

Vue.filter('solveTime', time => {
  let _time = dateFtt(time)
  return _time
})

Vue.filter('solveResult', status => {
  let statusList = {0:'待处理',1:'处理完成',2:'无法处理'}
  return statusList[status]
})

Vue.filter('solveCustomerStatus', status => {
  let name = status == 0?'意向客户':''
  return name
})
