import service from "./http";
import { filterNullProp, isVaild, formData} from '@/utils/tools'

// 自动化导入module下的js文件
const moduleFiles = require.context("./module",false, /\.js$/)
const modules = moduleFiles.keys().reduce((module, modulePath) => {
  const moduleName = modulePath.replace(/^\.\/(.*)\.\w+$/, '$1')
  const value = moduleFiles(modulePath)
  module[moduleName] = value.default
  return module
},{})

export const Apis = modules
export const reqBody = ({url,method='GET',params}, config) => {
  params = isVaild(params) && filterNullProp(params)
  if (method == 'GET') {
    return service(url, {params, config})
  } else if(method == 'POST') {
    if (config?.type == 'export') {
      return service({
        url,
        method,
        data: params,
        responseType: 'blob',
        config
      })
    } else {
      return service({
        url,
        method,
        data: params,
        config
      })
    }
  }
}

