<template>
  <div id="app">
    <router-view v-if="isRouterAlive"/>
  </div>
</template>
<script>
  export default {
    name: 'App',
    provide() {
      return {
        reload: this.reload
      }
    },
    data() {
      return {
        isRouterAlive: true
      }
    },
    methods: {
      reload() {
        let _this = this
        _this.isRouterAlive = false
        _this.$nextTick(() => {
          _this.isRouterAlive = true
        })
      }


    }
  }
</script>
<style>
  #app{
    height: 100%;
  }
</style>
