<template>
  <el-dialog
    title="新增跟进记录"
    :visible.sync="dialogFollowVisible"
    :close-on-click-modal="false"
    :close-on-press-escape="false"
    :before-close="cancelFollow"
    class="add-follow">
    <el-form :model="followForm" ref="followForm" >
      <el-form-item label="客户名称" prop="customerName">
        <el-autocomplete
          v-model="followForm.customerName"
          value-key="customerName"
          :fetch-suggestions="(queryString, cb) => queryInputAsync(queryString, cb, 'customer')"
          :placeholder="'input' | solvePlaceholder"
          @select="(val) => queryInputAsync('', val,'customer', true)"
        />
      </el-form-item>
      <el-form-item label="跟进人" prop="staffName">
        <el-autocomplete
          v-model="followForm.staffName"
          value-key="name"
          :fetch-suggestions="(queryString, cb) => queryInputAsync(queryString, cb, 'followMan')"
          :placeholder="'input' | solvePlaceholder"
          @select="(val) => queryInputAsync('',val,'followMan', true)"
        />
      </el-form-item>
      <el-form-item label="跟进结果" prop="result">
        <el-input v-model="followForm.result" :placeholder="'input' | solvePlaceholder" />
      </el-form-item>
    </el-form>

    <span slot="footer">
      <el-button type="primary" @click="saveFollow">保存</el-button>
      <el-button @click="cancelFollow">取消</el-button>
    </span>
  </el-dialog>
</template>
<script>
  import {Apis} from "@/axios";
  import {Message} from 'element-ui'

  export default {
    name: 'AddFollow',
    data() {
      return {
        followForm: {
          customerId:'',
          customerName:'',
          phone:'',
          staffId:'',
          staffName:'',
          result: '',
        }
      }
    },
    props: {
      dialogFollowVisible: {
        type: Boolean,
        default: false
      },
      parentId: {
        type: Number,
        default: null
      }
    },
    inject: ['reload'],
    methods: {
      queryInputAsync(keyWord, cb, type, isSelect) {
        let _this = this
        let {searchCustomer, searchStaff} = Apis.member
        let requestUrl = type == 'customer' ? searchCustomer : type == 'followMan' ? searchStaff : null
        if (type == 'customer') {
          if (!isSelect) {
            requestUrl({keyWord}).then(res => {
              if (res.data.status == 200) cb(res.data.data)
              else Message.error(res.data.msg)
            })
          } else {
            let {id, customerName, phone} = cb
            _this.followForm.customerId = id
            _this.followForm.customerName = customerName
            _this.followForm.phone = phone
          }
        } else if(type == 'followMan') {
          if (!isSelect) {
            requestUrl({keyWord}).then(res => {
              if (res.data.status == 200) cb(res.data.data)
              else Message.error(res.data.msg)
            })
          } else {
            let {id, name} = cb
            _this.followForm.staffId = id
            _this.followForm.staffName = name
          }
        }
      },

      saveFollow() {
        let _this = this
        let {flowerCustomerSave} = Apis.member
        _this.followForm.parentId = _this.parentId
        flowerCustomerSave(_this.followForm).then(res => {
          if (res.data.status == 200) {
            Message.success(res.data.msg)
            _this.$route.name == 'FollowUp'&&_this.reload()
            _this.$refs['followForm'].resetFields()
            _this.followForm = {
              customerId:'',
              phone:'',
              staffId:'',
              result: '',
            }
            _this.$emit('callback', false)
          } else Message.error(res.data.msg)
        })
      },
      cancelFollow() {
        let _this = this
        _this.$refs['followForm'].resetFields()
        _this.followForm = {
          customerId:'',
          phone:'',
          staffId:'',
          result: '',
        }
        _this.$emit('callback', false)
      }
    }
  }
</script>
