// 判断是否为一个对象
export const isObject = value => (Object.prototype.toString.call(value) === "[object Object]")

// 判断是否为一个非空非undefined非null的值
export const isVaild = value => !whiteEmptyVal.includes(value)

// 返回错误提示
export const errorTip = error => error || '该项为必填项'

// sessionStorage
export const operaStorage = (type, key, value) => {
  let _storage
  switch (type) {
    case 'set':
      sessionStorage.setItem(key, value)
      break;
    case 'get':
      _storage = sessionStorage.getItem(key)
      break;
    case 'remove':
      sessionStorage.removeItem(key)
      break;
    case 'clear':
      sessionStorage.clear()
      break;
    default:
      _storage = false
      break;
  }
  return _storage
}

// 格式化时间
export const dateFtt = (date, fmt="yyyy-MM-dd") => {
  console.log('date', date)
  date = new Date(date)
  let o = {
    "M+" : date.getMonth()+1,     //月份
    "d+" : date.getDate(),     //日
    "h+" : date.getHours(),     //小时
    "m+" : date.getMinutes(),     //分
    "s+" : date.getSeconds(),     //秒
    "q+" : Math.floor((date.getMonth()+3)/3), //季度
    "S" : date.getMilliseconds()    //毫秒
  };
  if(/(y+)/.test(fmt))
    fmt=fmt.replace(RegExp.$1, (date.getFullYear()+"").substr(4 - RegExp.$1.length));
  for(let k in o)
    if(new RegExp("("+ k +")").test(fmt))
      fmt = fmt.replace(RegExp.$1, (RegExp.$1.length==1) ? (o[k]) : (("00"+ o[k]).substr((""+ o[k]).length)));
  return fmt;
}

// 判断是否为一个空对象
export const isEmptyObject = obj => {
  if (isObject(obj)) {
    for (const key in obj) {
      if (!whiteEmptyVal.includes(obj[key])){
        return false
      }
    }
    return true
  }
}

// 解析树结构数据
let _result = []
export const treeToArray = trees => {
  trees.forEach(t => {
    let tx = {deptName: t.deptName,id: t.id, parentId: t.parentId}
    _result.push(tx)
    // _result = removeDuplicateObj(_result)
    if (t.children&&t.children.length) {
      treeToArray(t.children)
    }
  })
  return _result
}
// 数组对象去重
export const removeDuplicateObj = (arr, sameKey) => {
  let obj = {};
  arr = arr.reduce((newArr, next) => {
    obj[next[sameKey]] ? "" : (obj[next[sameKey]] = true && newArr.push(next));
    return newArr;
  }, []);
  return arr;
};

