import {Message} from "element-ui";
import {operaStorage} from '@/utils/tools'

export default {
  namespaced: true,
  state: ()=>({
    isLogin: operaStorage('get', 'isLogin'),
    token: operaStorage('get', 'Authorization'),
    userInfo: JSON.parse(operaStorage('get', 'userInfo')||'{}')
  }),
  actions: {
    _userInfo({commit}, args) {
      commit('UserInfo', args)
    }
  },
  mutations: {
    UserInfo(state, args) {
      let {getInfo} = args
      getInfo().then(res => {
        if (res.data.status == 200) {
          operaStorage('set','userInfo',JSON.stringify(res.data.data))
        } else Message.error(res.data.msg)
      })
    },
    set_token(state, val) {
      operaStorage('set', 'Authorization', val)
      state.token = val
    },
    set_isLogin(state, val) {
      operaStorage('set', 'isLogin', val)
      state.isLogin = val
    },
    set_logout(state) {
      operaStorage('clear')
      state.isLogin = null
      state.token = null
      state.userInfo = {}
    }
  }
}
